"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { PlusCircle } from "lucide-react"
import { Button } from "@/components/ui/button"
import { PostsTable } from "@/components/dashboard/postsTable"
import { PostsCardView } from "@/components/dashboard/postsCardView"
import { getPosts, PostRead, deletePost } from "@/services/postService"

export default function PostsPage() {
  const [posts, setPosts] = useState<PostRead[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchPosts = async () => {
      try { 
        const response = await getPosts()
        setPosts(response.data.items)
      } catch (error) {
        setError("Ошибка при загрузке постов.")
      } finally {
        setIsLoading(false)
      }
    }
    fetchPosts()
  }, [])

  const handleDelete = async (id: number) => {
    try {
      await deletePost(id)
      setPosts(posts.filter((post) => post.id !== id))
    } catch (error) {
      setError("Ошибка при удалении поста.")
    }
  }

  return (
    <div>
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-6 gap-4">
        <h1 className="text-2xl md:text-3xl font-bold">Посты</h1>
        <Button asChild>
          <Link href="/dashboard/posts/new">
            <PlusCircle className="mr-2 h-4 w-4" /> Новый пост
          </Link>
        </Button>
      </div>
      <div className="hidden md:block">
        <PostsTable posts={posts} onDelete={handleDelete} />
      </div>
      <div className="md:hidden">
        <PostsCardView posts={posts} onDelete={handleDelete} />
      </div>
    </div>
  )
}

